.how-it-works__wrapper {
  .content-area;
  padding: 0 20px;
}

.how-it-works__heading {
  font-size: 36px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.57;

  margin: 0 0 30px;
  padding: 0;

  letter-spacing: normal;
}

.how-it-works__list {
  margin: 0;
  margin-bottom: 43px;
  padding: 0;

  list-style: none;
}

.how-it-works__item {
  display: flex;

  margin: 0 0 20px;
  padding: 0;

  &:last-of-type {
    margin: 0;
  }
}

.how-it-works__icon {
  background-color: @leaf-green;

  position: relative;

  width: 60px;
  height: 60px;
  margin: 0;
  margin-right: 20px;
  padding: 0;

  flex-wrap: nowrap;
  flex-shrink: 0;
}

.how-it-works__icon--leaf {
  &::after {
    position: absolute;
    top: 0;
    right: 0;

    width: 60px;
    height: 60px;

    content: "";

    background-image: url("../img/advantage-leaf@1x.png");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 50px 50px;
  }
}

.how-it-works__icon--chemistry {
  &::after {
    position: absolute;
    top: 0;
    right: 0;

    width: 60px;
    height: 60px;

    content: "";

    background-image: url("../img/advantage-powder@1x.png");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 50px 50px;
  }
}

.how-it-works__icon--cutlery {
  &::after {
    position: absolute;
    top: 0;
    right: 0;

    width: 60px;
    height: 60px;

    content: "";

    background-image: url("../img/advantage-dining@1x.png");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 50px 50px;
  }
}

.how-it-works__icon--weighing-machine {
  &::after {
    position: absolute;
    top: 0;
    right: 0;

    width: 60px;
    height: 60px;

    content: "";

    background-image: url("../img/advantage-clock@1x.png");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 50px 50px;
  }
}

.how-it-works__description {
  color: @grey-dark;

  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 1.29;

  margin: 0;
  padding: 3px 0;

  flex-wrap: nowrap;
}

@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .how-it-works__icon--leaf {
    &::after {
      background-image: url("../img/advantage-leaf@2x.png");
    }
  }

  .how-it-works__icon--chemistry {
    &::after {
      background-image: url("../img/advantage-powder@2x.png");
    }
  }

  .how-it-works__icon--cutlery {
    &::after {
      background-image: url("../img/advantage-dining@2x.png");
    }
  }

  .how-it-works__icon--weighing-machine {
    &::after {
      background-image: url("../img/advantage-clock@2x.png");
    }
  }
}

@media (min-width: 768px) {
  .how-it-works__wrapper {
    .content-area--tablet;
    padding: 0 30px;
  }

  .how-it-works__heading {
    font-size: 60px;
    line-height: 1;
    margin: 0 0 40px;
  }

  .how-it-works__list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-bottom: 25px;
    counter-reset: list;
  }

  .how-it-works__item {
    width: 324px;
    box-sizing: border-box;
    flex-direction: column;
    flex-shrink: 1;
    min-height: 235px;
    padding-top: 32px;
    margin: 0 58px 82px 0;
    position: relative;
    list-style-type: none;
    counter-increment: list;

    &::after {
      content: counter(list);
      position: absolute;
      font-size: 281px;
      line-height: 281px;
      color: #f2f2f2;
      top: -7px;
      right: 79px;
      z-index: -1;
    }

    &:nth-of-type(even) {
      margin-right: 0;
    }

    &:first-of-type {
      &::after {
        right: 96px;
      }
    }
  }

  .how-it-works__description {
    width: 245px;
    font-size: 16px;
    line-height: 1.5;
    padding: 0;
  }

  .how-it-works__icon {
    width: 80px;
    height: 80px;
    margin: 0 0 32px;

    &::after {
      width: 80px;
      height: 80px;
    }
  }
}

@media (min-width: 1300px) {
  .how-it-works__wrapper {
    .content-area--desktop;
    padding: 0 40px;
    margin-bottom: 107px;
  }

  .how-it-works__list {
    margin-bottom: 0;
  }

  .how-it-works__item {
    width: 245px;
    margin-right: 80px;
    margin-bottom: 0;

    &::after {
      right: -2px;
    }

    &:nth-of-type(even) {
      margin-right: 80px;
    }

    &:nth-of-type(4n) {
      margin-right: 0;
    }

    &:first-of-type {
      &::after {
        right: 16px;
      }
    }
  }
}
